omputed(() => {\n    return viewModel.user?.selectedOrganizationGroup?.organizationGroup.mainAppId == viewModel.studio.appId\n})\n\nconst props = defineProps<IModuleItem>();\nconst emit = defineEmits([\"close\"]);\nconst shareOrgType = ref<\"Organization\" | \"Organization Group\">(\"Organization\");\nconst shareType = ref<ModuleShareType>(props.module.shareType!);\n\nfunction close() {\n    emit('close');\n}\nfunction selectShareType(type: ModuleShareType) {\n    // if (shareOrgType.value == \"Organization Group\") {\n    //     return;\n    // }\n    shareType.value = type;\n}\nfunction share() {\n    executor.execute(async () => {\n        await moduleVersionUseCase.shareModule(props.module.ID, { shareType: shareType.value });\n        close();\n        if (shareType.value == \"version\") {\n            openReleaseVersionDialog();\n        }\n    }, { loading: true })\n}\nfunction openReleaseVersionDialog() {\n    dialog.showDialog(defineAsyncComponent(() => import(\"./releaseModuleVersion.vue\")),\n        { closable: true, title: 'Release ' + props.module.name + ' Version', closeOnOutClick: false, width: '660px' }, { module: props.module }\n    )\n}\nfunction onSelectShareType(selectType: 'Organization' | 'Organization Group') {\n    switch (selectType) {\n        case \"Organization\":\n            shareOrgType.value = \"Organization\";\n            break;\n        case \"Organization Group\":\n            shareOrgType.value = \"Organization Group\";\n            shareType.value = \"version\";\n            break;\n        default:\n            break;\n    }\n}\n</script>\n<template>\n    <div class=\"container\">\n        <div>\n            <span class=\"share-org-title\">Share Type</span>\n            <CustomSelect v-if=\"isMainAppSelected\" :options=\"['Organization', 'Organization Group']\"\n                class=\"share-org-select\" :default=\"shareOrgType\" @input=\"onSelectShareType\"></CustomSelect>\n            <div class=\"share-info\" v-if=\"shareOrgType == 'Organization Group'\">\n                <i class=\"mdi mdi-information\"></i>\n                <span>You can only select and share version modules.</span>\n            </div>\n        </div>\n        <div style=\"margin-top: 20px;\">\n            <div class=\"share-type\">\n                <RadioButtonComp class=\"radio-btn\" :active=\"shareType == 'reference'\" :type=\"'preview'\"\n                    :disabled=\"shareOrgType == 'Organization Group'\" @click=\"selectShareType('reference')\" />\n                <span :class=\"shareOrgType == 'Organization Group' ? 'disabled' : ''\">Reference</span>\n            </div>\n            <div class=\"share-type\">\n                <RadioButtonComp class=\"radio-btn\" :active=\"shareType == 'version'\" :type=\"'preview'\"\n                    @click=\"selectShareType('version')\" />\n                <span>Version</span>\n            </div>\n        </div>\n        <div class=\"buttons-div\">\n            <button class=\"cancel-button\" @click=\"close()\">Cancel</button>\n            <button class=\"share-button\" @click=\"share()\">SHARE</button>\n        </div>\n\n    </div>\n</template>\n<style scoped>\n@import url(\"../../../../../../src/styles/IDE3.css\");\n\n.share-type .disabled {\n    color: #D5D5D5;\n}\n\n.share-org-title {\n    font-size: 15px;\n    font-weight: 500;\n    line-height: 18px;\n    margin-bottom: 3px;\n}\n\n.share-org-select {\n    width: 100%;\n    font-size: 15px;\n    height: 40px;\n    line-height: 40px;\n    border: 1px solid #BCBCBC;\n    border-radius: 4px;\n    padding: 0px 12px;\n}\n\n.container {\n    padding: 32px 20px 20px;\n}\n\n.share-info {\n    padding: 5px 12px;\n    margin-top: 20px;\n    width: 100%;\n    color: #757575;\n    font-size: 14px;\n    background: #F5F5FA;\n    border-radius: 4px;\n    display: flex;\n    align-items: center;\n}\n\n.share-info i {\n    font-size: 24px;\n    color: #151233;\n    margin-right: 8px;\n}\n\n.share-type {\n    margin-bottom: 20px;\n    display: flex;\n    align-items: center;\n    padding: 0 12px;\n}\n\n.share-type span {\n    color: #212121;\n    font-size: 14px;\n}\n\n.radio-btn {\n    margin-right: 10px;\n}\n\n.buttons-div {\n    display: flex;\n    justify-content: end;\n    margin-top: 52px;\n}\n\n.cancel-button {\n    font-weight: 500;\n    font-size: 16px;\n    text-align: center;\n    color: #757575;\n    display: flex;\n    align-items: center;\n    padding: 8px 16px;\n    border-radius: 4px;\n    margin-right: 4px;\n    border: none;\n    background: transparent;\n}\n\n.share-button {\n    font-weight: 500;\n    font-size: 16px;\n    text-align: center;\n    color: #FFFFFF;\n    background: #151233;\n    display: flex;\n    align-items: center;\n    padding: 8px 16px;\n    border-radius: 4px;\n    border: none;\n}\n\n.share-button:disabled {\n    color: #757575;\n}\n</style>"],"sourceRoot":""}]);
